.logo {
  display: inline-flex;
  align-items: center;
  cursor: pointer;

  &__svg {
    fill: none;
    stroke: #f1b600;
    transition: 0.2s ease;
  }

  &:hover .logo__svg {
    stroke: #e8e8e8;
  }

  &:hover p {
    color: #e8e8e8;
  }

  &:hover .logo__img {
    transform: rotate(180deg);
  }

  &__img {
    transition: 1s ease;
  }

  &__title_wrapper {
    margin-left: 0.4rem;
  }

  &__title {
    transition: 0.2s ease;
    color: #f1b600;
    font-size: 20px;
    font-weight: 500;
    margin: 0;
  }
}

@media (max-width: 576px) {
  .logo__title_wrapper {
    display: none;
  }
}