.button_back {
  display: inline-flex;
  align-items: center;
  color: #404040a3;
  cursor: pointer;
  transition: 0.1s ease;

  svg {
    height: 12px;
  }

  &:hover {
    color: #404040;
  }

  &_offset {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}