.sort{box-shadow:0 0 20px rgba(64,64,64,.2117647059);padding:1.3rem;margin-top:18px;display:flex;align-items:center;gap:1rem}.sort__item{cursor:pointer;transition:.2s ease}.sort__item:hover{transform:translateY(-3px)}.sort__item_active{font-weight:600}.sort__container{position:relative;margin-top:18px;cursor:pointer}.sort__container::after{content:"▽";position:absolute;top:26%;right:10%;width:0px;height:0px;font-size:20px}.sort__mobile{padding:.7rem;width:100%;appearance:none;border:none;box-shadow:0 0 20px rgba(64,64,64,.2117647059);outline:none;background:rgba(0,0,0,0);cursor:pointer}